body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

div .workspace {
    background-color: #e6e6e6;
    width: 100vw;
    height: 100vh;
    cursor: pointer;
}

div .note {
    position: absolute;
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;
    border: solid 1px #ddd;
    background: #f0f0f0;
}

div .note-header {
    align-self: start;
    width: 100%;
    height: 15%;
    max-height: 35px;
}

div .note-header .cross {
    position: absolute;
    right: 14px;
    top: 5px;
    cursor: pointer;
    display: flex;
    transition: all 450ms;
}

div .note-header .cross:hover {
    transition: all 450ms;
    transform: scale(2);
}

div.color-buttons {
    opacity: 0;
    display: flex;
    height: 100%;
    width: 40%;
    transition: opacity 1s;
}

div.color-buttons > button {
    align-self: center;
    margin-left: 5px;
}

div.color-buttons > button.blue {
    background-color: #42aaff;
}

div.color-buttons > button.pink {
    background-color: #fb607f;
}

div.color-buttons > button.yellow {
    background-color: yellow;
}


div.color-buttons:hover {
    opacity: 1;
    transition: opacity 1s;
}

div.note-body {
    display: inherit;
    overflow: auto;
    word-break: break-word;
    width: 100%;
    height: 90%;
}

textarea.note-text {
    display: inherit;
    overflow: auto;
    width: 100%;
    height: 90%;
    resize: none;
}